<template>
	<view>
		<view class="status-bar">
			<!-- open-type="navigateBack"：跳到上一页面或多级页面 -->
			<navigator open-type="navigateBack" class="login-c" hover-class="none"> 
				<image src="../../static/back.png" class="login-back"></image> 
			</navigator>
			<!-- <view>智慧医疗小助手</view> -->
		</view>
		<view class="main">
			<view v-for="(e,index) in chat" :key="index">
				<!-- 客户端-->
				<view class="right msg" v-if="e.fid == 1">
					<image :src="'../../static/images/'+e.img+'.png'"></image>
					<view class="nr">
						<view class="neir">
							{{e.neir}}
						</view>
						<view class="nt">{{timeDetia(e.time)}}</view>
					</view>
				</view>
				<!-- 服务器 -->
				<view class="left msg" v-if="e.fid==0">
					<image :src="'../../static/images/'+e.img+'.png'"></image>
					<view class="nr">
						<view class="neir">
							{{e.neir}}
						</view>
						<view class="nt">{{timeDetia(e.time)}}</view>
					</view>					
				</view>
				<!-- 欢迎用户 -->
				<view class="center" v-if="e.id==3">
					<view class="inner">{{e.name}}</view>				
				</view>
			</view>	
		</view>
		<view class="send">
			<textarea type="text" confirm-type="send" class="chat-send"  v-model="chatm" auto-height="true" show-confirm-bar="false" maxlength="-1" />
			<image src="../../static/send.png" mode="aspectFit" @tap="sendMsg"></image><!-- @tap 点击时触发 -->
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				chatm:'',			//消息内容
				name:'',			//用户名称
				uname:'',
				fid:'',
				uid:'a',
				img:'',				//用户头像
				chat:[],			//聊天内容
				member:0,			//成员个数
				animationData: {},
				bb:false,
				bg:'none',
				users:''
			}
		},
		onLoad: function (e) {
			this.name = e.name;
			this.img = e.img;
			this.join(this.name,this.img);
		},
		methods: {
			timeDetia: function(date){
				var time;
				var d = new Date(date);
				var n = new Date();
				//获取时间戳
				var dd = d.getTime();
				var h = d.getHours();
				var m = d.getMinutes();
				var Y = d.getFullYear();
				var M = d.getMonth() + 1;
				var D = d.getDate();
				//现在时间
				var nn = n.getTime();
				var hh = n.getHours();
				var mm = n.getMinutes();
				var YY = n.getFullYear();
				var MM = n.getMonth() + 1;
				var DD = n.getDate();
				
				if( D == DD && M == MM && Y ==YY){
					if(h<10){
					h = '0'+h;
					}
					if(m<10){
						m = '0'+m;
					}
					time = h+':'+m;
					return time;
				}else if(D+1 == DD && M == MM && Y ==YY){
					if(h<10){
					h = '0'+h;
					}
					if(m<10){
						m = '0'+m;
					}
					time = '昨天'+' '+h+':'+m;
					return time;
				}else{
					if(M<10){
						M = '0'+M;
					}
					if(D<10){
						D = '0'+D;
					}
					if(h<10){
					h = '0'+h;
					}
					if(m<10){
						m = '0'+m;
					}
					time = Y+'年'+M+'月'+D+'日'+' '+h+':'+m;
					return time;
				}
			},
			//发送消息
			sendMsg: function(){
				if(this.chatm.length>0){ 
					let onemsg = {
						neir:this.chatm,
						img:this.img, // 客户端头像
						time:new Date(),
						fid:1, // 客户端
					}
					this.chat.push(onemsg); 
					this.chatm = '';//清空
					// 给服务器发消息
					this.$axios({
					    method: "get",//指定请求方式
						url:"http://222.29.68.23:30108/medical/ask/answerQuestion.do",
					    params: {
					        query: onemsg.neir // 问题作为参数
					      }
					  })
					  .then(res=>{
						  let msgres = '服务器消息'
						  
						  // // 提取服务器回复给 msgres
						  // res = res.data.data.integrationResults
						  // console.log(this.$jquery.parseJSON(res)[2])
						  // let dict = this.$jquery.parseJSON(res)[2].row_datas[0]
						  // for(let key in dict){
							 //  if(key=='e_1.answer'){
								// msgres = dict[key][0]
								// break  
							 //  }
						  // }
						  // 接收服务器消息 msg
						  let data = {
						  	neir:msgres,
						  	time:new Date(),
						  	img:this.img, // 服务器头像
						  	fid:0, // 服务器
						  }
						  // 显示服务器传来的消息
						  this.chat.push(data);
					  })
				}		
			},
			// 欢迎用户
			join: function(name,img){
				let wel = {
					name:'欢迎 '+name,
					id:3, // 3则显示到中间
				}
				this.chat.push(wel);
			}
		}
	}
</script>

